<template>
    <div class="user-manager">
        <!-- 用户表格 -->
         <el-table
            v-loading="listLoading"
            :data="list"
            element-loading-text="Loading"
            border
            fit
            highlight-current-row
            >
            <el-table-column align="center" label="用户ID" width="400" >
                <template slot-scope="scope">
                {{ scope.row.userId}}
                </template>
            </el-table-column>
             <el-table-column label="用户状态" width="400"  align="center">
                <template slot-scope="scope">
                    <span :style="{'color':scope.row.status?'#67C23A':'#F56C6C'}"> 
                        {{scope.row.status?'正常':'禁用'}}</span>                
                </template>
            </el-table-column>
            <el-table-column label="操作" width="428"  align="center">
                  <template slot-scope="scope">
                    <el-button
                    size="mini"
                    type="primary"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button
                    size="mini"
                    type="warning"
                    @click="handleBan(scope.$index, scope.row)">
                       {{scope.row.status?'封号':'解禁'}}
                    </el-button>
                </template>
            </el-table-column>
            </el-table>
         <!-- 用户表格结束 -->

         <!-- 用户编辑框 -->
         <el-dialog :title="showText" :visible.sync="dialogFormVisible">
            <el-form :model="editInfo">
                <el-form-item label="用户Id" :label-width="formLabelWidth" >
                  <el-input v-model="editInfo.userId" autocomplete="off" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="用户状态" :label-width="formLabelWidth">
                     <el-radio v-model="editInfo.status" :label="1" >正常</el-radio>
                     <el-radio v-model="editInfo.status" :label="0" > 封禁</el-radio>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="updateUserInfo()">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import {getAllUser,banUser, unBanUser,deleteUser} from '../../network/userAndAdmin'
export default {
    data(){
        return{
            list: [

            ],
            listLoading: false,
            dialogFormVisible:false,
            currentIndex:'',
            showText:'添加训练用户',
            editInfo:{
               userId:'',
               password:'',
               status:false
            },
            formLabelWidth: '120px'
        }
        
    },
    methods:{
        //处理解封禁
        async handleBan(index,row)
        {
            console.log(index,row);
            const id=row.userId;
            if(row.status)
            {
                    let confirm=await this.$confirm(`此操作将封禁用户ID为"${id}"的账号, 是否继续?`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    });
                    if(confirm=='confirm')
                    { 
                        let res=await banUser(row.userId);
                        await this.getAllUser();
                        this.$message({
                            type: 'success',
                            message: '封禁成功!'
                        });
                    }                 
            }
            else
            {
                  let confirm=await this.$confirm(`此操作将解封用户ID为"${id}"的账号, 是否继续?`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    })
                  if(confirm=='confirm')
                  {
                      let res=await unBanUser(row.userId);
                      await this.getAllUser();
                  }
            }
                
        },
        handleEdit(index, row)
        {
            const tutoId=row.tutoId;
            console.log(tutoId);
            this.dialogFormVisible=true;
            this.$turn(this.editInfo,this.list[index])
            this.editInfo.status=row.status;
            this.showText='修改用户信息';
            this.currentIndex=index;
            console.log(this.editInfo);
        },
        clearInfo()
        {
           this.editInfo.titleContent='';
           this.editInfo.titleNum='';
           this.editInfo.content='';
        },
        async handleDelete(index,row) {
            console.log(index,row);
            const id=row.userId;
            let confirm=await this.$confirm(`此操作将永久删除用户"${id}", 是否继续?`, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            });
            console.log(confirm);
            if(confirm=='confirm')
            {
                let res=await deleteUser(row.userId);
                await this.getAllUser();
                this.$message({
                    type:'success',
                    message:'删除用户成功'
                })
            }

      },
      //更新用户信息
      updateUserInfo()
      {
          console.log(this.editInfo);
          this.$turn(this.list[this.currentIndex],this.editInfo);
          if(!this.editInfo.status)
          this.list[this.currentIndex].status=false;
         this.dialogFormVisible=false;
         this.$message({
            message:'更新用户信息成功！',
            type:'success'
         })
      },
      //获取所有用户
      async getAllUser()
      {
          let res=await getAllUser();
          console.log(res);
          this.list=res.data.data.userList;
      }
    },
    created(){
        this.getAllUser();
    }
}
</script>

<style scoped>
     .user-manager
     {
         width: 100%;
         position: relative;
     }
    
</style>