//用户封号
export function banUser(userId) {
    return request({
        method: 'put',
        url: '/user/ban',
        data: {
            userId
        }
    })
}

//解除封号
export function unBanUser(userId) {
    return request({
        method: 'put',
        url: '/user/unban',
        data: {
            userId
        }
    })
}


//获取所有用户名
export function getAllUser() {
    return request({
        url: '/user/getAll',
        method: 'get',
    })
}

//删除用户
export function deleteUser(userId) {
    return request({
        url: `/user`,
        method: 'delete',
        data: {
            userId
        }
    })
}